import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Scroll } from 'ionic-angular';

/**
 * Generated class for the Preference page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-preference',
  templateUrl: 'preference.html',
})
export class Preference {
  slideDatas: any;
  @ViewChild('Sliders') sliders: Scroll;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }
  ionViewDidEnter() {
    this.slideDatas = [
      {
        image: 'url(assets/img/foody-mobile-12.jpg)',
        title: 'Protien',
        content: 'Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum',
        like: true
      },
      {
        image: 'url(assets/img/reference_pic1.png)',
        title: 'Protien',
        content: 'Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum',
        like: false
      },
      {
        image: 'url(assets/img/ingredient_pic1.png)',
        title: 'Protien',
        content: 'Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum',
        like: false
      },
      {
        image: 'url(assets/img/foody-mobile-hokkaido.jpg)',
        title: 'Protien',
        content: 'Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum',
        like: true
      },
      {
        image: 'url(assets/img/1211_awards_restaurant.jpg)',
        title: 'Protien',
        content: 'Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum Lorem ipsum',
        like: false
      },
    ]
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad Preference');

    this.sliders.addScrollEventListener((event) => {
      var windowX = window.innerWidth;
      var nbItems = 4;
      var sectionWidth = windowX / nbItems;
      var slides = document.getElementsByClassName('preference-card');

      for (var i = 0; i < slides.length; i++) {
        var x = slides[i].getBoundingClientRect().left + sectionWidth / 2

        if (x < sectionWidth && slides[i] != slides[slides.length - 1]) {
          slides[i].classList.add('blur-card');
        }
        else if (x > sectionWidth * 2 && slides[i] != slides[0]) {
          slides[i].classList.add('blur-card');
        }
        else {
          slides[i].classList.remove('blur-card');
        }
      }

    });
  }

  likeToggle(slide: any) {
    slide.like = !slide.like;
  }
}



